import React, { Component } from 'react';
import { BrowserRouter, Route, Switch, withRouter, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';


import Layout from './components/Layouts/Layout';
import Home from './components/Home/Home';
import Friends from './components/Friends/Friends';
import Landing from './components/Landing/Landing';
import Register from './components/Register/Register';
import Login from './containers/Login/Login';
import CbLogin from './Callback/CbLogin';
import Admin_WalmartCategories from './containers/Admin/Admin_WalmartCategories';


class App extends Component {
    render() {
        return (
            <Switch>
                <Route path='/login' component={Login} />
                <Route path='/register' component={Register} />
                <Route path="/cblogin" component={CbLogin} />
                <Route path='/friends' component={Friends} />
                <Route path='/home' component={Home} />
                <Route path="/admin-walmart-categories" component={Admin_WalmartCategories} />
                <Route exact path='/' component={Landing} />

            </Switch>


        );
    }
}



export default withRouter(App);




